<template>
  <div class="launcher">

    <div class="search"
      :style="{opacity: (this.appFilter === '')?0.5:1} "
    >
      <div class="has-icon-right">
        <input type="text" class="form-input" placeholder="Search" v-model="appFilter">

        <i class="form-icon icon icon-cross  c-hand"
          v-if="appFilter !== ''"
          @click="clearFilter"
        ></i>

        <i class="form-icon icon icon-search"
          v-if="appFilter === ''"
        ></i>

      </div>
    </div>

    <div class="app-icons">
      <div class="app-icon c-hand"
        v-for="app in filteredApps"
        :key="app.id"
        @click="launchApp(app.id)"
      >
        <img class="app-icon-image" :src="app.icon" alt="">
        <h6 class="app-name">
          {{app.name}}
        </h6>
      </div>

    </div>

  </div>

</template>

<script>

export default {
  name: 'AppLauncher',
  data () {
    return {
      appFilter: ''
    }
  },
  methods: {
    launchApp: function (appId) {
      this.$store.dispatch('launchApp', {appId: appId})
    },
    clearFilter: function () {
      this.appFilter = ''
    }
  },
  computed: {
    filteredApps: function () {
      return this.$store.getters.availableApps.filter(function (app) {
        if (this.appFilter === '') {
          return true
        } else {
          return app.name.match(new RegExp(this.appFilter, 'i'))
        }
      }.bind(this))
    }
  }
}
</script>

<style scoped>
.launcher {
  flex: 1;
  height: 100%;
  overflow-y: auto;
}

.search{
  width: 280px;
  position: sticky;
  top: 20px;
  margin: auto;
}

.app-icons {
  display: flex;
  flex-wrap: wrap;
  max-width: 800px;
  margin: auto;
  margin-top: 40px;
}

.app-icon {
  margin: 20px;
  display: flex;
  flex-direction: column;
}

.app-icon-image {
  margin: auto;
  /* height: 128px; */
  /* width: 128px; */
  height: 128px;
  width: 128px;
  margin-bottom: 20px;

  padding: 10%;
}

.app-name {
  color: #ecf0f1;
}

@media screen and (max-width: 768px) {
  .app-icons {
    /* height: 64px; */
    /* width: 64px; */
    justify-content: center;
  }
}
</style>



